.container {
    border: 1px solid black;
    box-sizing: border-box;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    padding: 20px;
    display: flex;
    align-items: center;
    flex-flow: column wrap;
    justify-content: flex-start;
}

@media screen and (max-width: 500px) {
    .container {
        width: 100%;
    }
}

.itemsContainer {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
}