.mainBlock {
    background: #a453a4;
}

.mainContainer {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}



.text {
    width: 300px;
    background: #fff;
}

@media screen and (max-width: 830px) {
    .mainContainer {
        flex-flow: column wrap;
        justify-content: center;
    }

    .text {
        margin-bottom: 20px;
    }

}

.photo {
    width: 300px;
    height: 400px;
    background: #fff;
}

