.distantContainer {
    background: lightgreen;
}

.distantSubContainer {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    border: 1px solid;
    padding: 0px 90px;
}

@media screen and (max-width: 600px) {
    .distantSubContainer {
        padding: 0px 20px;
    }

}

.btn {
    padding: 10px;
    background: gray;
    cursor: pointer;
}

.btn:hover {
    cursor: pointer;
}