.workContainer {
    margin: 10px 10px;
}

.imgContainer {
    width: 250px;
    height: 250px;
    background: gray;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    z-index: 1;
    object-fit: cover;
    opacity: 0.7;
}

.btn {
    position: relative;
    z-index: 2;
    background: #fff;
    padding: 10px;
    border-radius: 10px;
}

.infoContainer {
}

.projectName {
    background: gray;
}

.projectDescription {
    background: gray;
}